import React from "react"
import { graphql, Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const PostLink = ({ post }) => (
  <div style={{ margin: "1em 0 2em 1em" }}>
    <div
      style={{
        padding: "0 1em 1em 1em",
        borderLeft: "solid 1px gray",
        overflow: "hidden",
      }}
    >
      <h4>
        "<Link to={post.frontmatter.path}>{post.frontmatter.title}</Link>
        ", <small>posted on {post.frontmatter.date}</small>
      </h4>
      <p>{post.frontmatter.abstract}</p>
      <div style={{ fontStyle: "italic" }}>Tags: {post.frontmatter.tags}</div>
    </div>
  </div>
)

const IndexPage = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const Posts = edges
    .filter(edge => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
    .map(edge => <PostLink key={edge.node.id} post={edge.node} />)

  return (
    <Layout>
      <SEO title="Welcome" />
      <h2>Welcome to my website</h2>
      <p>
        It has been some time since I wanted to share my work and my thoughts.
        Well, here it is: hope you'll find it interesting !
      </p>
      <h2>My blog posts</h2>
      {Posts}
    </Layout>
  )
}

export default IndexPage
export const pageQuery = graphql`
  query HomePageQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "DD-MM-YYYY")
            path
            title
            abstract
            tags
          }
        }
      }
    }
  }
`
